import axios from '../utils/axios';

class BranchService {
  list = async () => {
    const branches = await axios.get('/api/v1/branches');
    return branches;
  };

  create = async (data) => {
    const branch = await axios.post('/api/v1/branches', data);
    return branch;
  };

  get = async (id) => {
    const branch = await axios.get(`/api/v1/branches/${id}`);
    return branch;
  };

  update = async (id, data) => {
    const branch = await axios.patch(`/api/v1/branches/${id}`, data);
    return branch;
  };

  delete = async (id) => {
    const branch = await axios.delete(`/api/v1/branches/${id}`);
    return branch;
  };
}

const branchService = new BranchService();

export default branchService;
