import axios from '../utils/axios';

class MemberService {
  list = async () => {
    const members = await axios.get('/api/v1/members');
    return members;
  };

  create = async (data) => {
    const member = await axios.post('/api/v1/members', data);
    return member;
  };

  get = async (id) => {
    const member = await axios.get(`/api/v1/members/${id}`);
    return member;
  };

  update = async (id, data) => {
    const member = await axios.patch(`/api/v1/members/${id}`, data);
    return member;
  };

  delete = async (id) => {
    const member = await axios.delete(`/api/v1/members/${id}`);
    return member;
  };

  detail = async (id) => {
    const member = await axios.get(`/api/v1/members/${id}/detail`);
    return member;
  };
}

const memberService = new MemberService();

export default memberService;
