import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { Box, Button, Card, CardContent, Grid, TextField, makeStyles } from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import { useTranslation } from 'react-i18next';
import useSnackbar from '../../../hooks/useSnackbar';
import Autocomplete from '../../../components/FormikAutocomplete';
import prepaidcardService from '../../../services/prepaidcardService';
import useMember from '../../../hooks/useMember';
import useBranch from '../../../hooks/useBranch';

const useStyles = makeStyles(() => ({
  root: {}
}));

function TopupForm({ className, ...rest }) {
  const classes = useStyles();
  const { successSnackbar } = useSnackbar();
  const { t } = useTranslation();
  const { members, getMemberFullName } = useMember();
  const { branches } = useBranch();

  if (!members.length) {
    return null;
  }

  if (!branches.length) {
    return null;
  }

  return (
    <Formik
      initialValues={{
        member: members[0],
        branch: branches[0],
        topup: 0,
        remark: ''
      }}
      validationSchema={Yup.object().shape({
        member: Yup.object().required(),
        branch: Yup.object().required(),
        topup: Yup.number().required(t('is required', { name: t('Topup') }))
      })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          await prepaidcardService.topup({
            ...values,
            phone: values.member.phone,
            branch_id: values.branch.code
          });
          setStatus({ success: true });
          setSubmitting(false);
          successSnackbar(t('Topup success'));
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.response.data.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values,
        setFieldValue
      }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit} {...rest}>
          <Card>
            <CardContent>
              {errors.submit && (
                <Box mb={3}>
                  <Alert severity="error">{errors.submit}</Alert>
                </Box>
              )}
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <Autocomplete
                    options={members}
                    getOptionLabel={(option) => getMemberFullName(option)}
                    value={values.member}
                    onBlur={handleBlur}
                    name="member"
                    onChange={(_, value) => setFieldValue('member', value)}
                    textFieldProps={{ required: true, variant: 'outlined', label: t('Member') }}
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <Autocomplete
                    options={branches}
                    getOptionLabel={(option) => `${option.name}`}
                    value={values.branch}
                    onBlur={handleBlur}
                    name="branch"
                    onChange={(_, value) => setFieldValue('branch', value)}
                    textFieldProps={{ required: true, variant: 'outlined', label: t('Branch') }}
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.topup && errors.topup)}
                    fullWidth
                    helperText={touched.topup && errors.topup}
                    label={t('Topup')}
                    name="topup"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.topup}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.remark && errors.remark)}
                    fullWidth
                    helperText={touched.remark && errors.remark}
                    label={t('Remark')}
                    name="remark"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.remark}
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button variant="contained" color="secondary" type="submit" disabled={isSubmitting}>
                  {t('Submit')}
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
}

TopupForm.propTypes = {
  className: PropTypes.string
};

export default TopupForm;
