import { useContext } from 'react';
import { useSnackbar as useBaseSnackbar } from 'notistack';

export default function useSnackbar() {
  const { enqueueSnackbar } = useBaseSnackbar();

  const successSnackbar = (text) => {
    enqueueSnackbar(text, {
      variant: 'success',
      anchorOrigin: {
        vertical: 'bottom',
        horizontal: 'right'
      }
    });
  };

  return { successSnackbar };
}
